<template>
	<div class="finatop" v-bind:style="{height: (prohvalue*0.6636-40)+'px'}">
		<div>
			<h4>总缴纳金额</h4>
			<span>
				<em>￥</em>
				<i>{{this.toptotalshow?(this.toptotalshow.gmv?(this.fmoney(this.toptotalshow.gmv/10000, 2)):'0.00'):'0.00'}}</i>
				<b>万</b>
			</span>
		</div>
		<div>
			<h4>优惠总金额</h4>
			<span>
				<em>￥</em>
				<i>{{this.toptotalshow?(this.toptotalshow.youhui?(this.fmoney(this.toptotalshow.youhui/10000, 2)):'0.00'):'0.00'}}</i>
				<b>万</b>
			</span>
		</div>
		<div>
			<h4>总缴纳笔数</h4>
			<span>
				<em></em>
				<i>{{this.toptotalshow?(this.toptotalshow.order_num?((Number(this.toptotalshow.order_num)).toLocaleString()):0):0}}</i>
				<b>笔</b>
			</span>
		</div>
	</div>
</template>

<script>
	export default {
	  name: 'Bossfintop',
	  props: ['toptotalshow'],
	  data () {
	    return {
	      prohvalue: 0,
	    }
	  },
	  methods: {
	  	//根据屏幕适配高度
		perHeight() {
		    let clientWidth = document.body.clientWidth - 80;
		    let height = (clientWidth / 3) * (9 / 16);
		    return height;
		},
		//数值千分位的同时保留两位小数，四舍五入
		fmoney(s, n){
			n = n > 0 && n <= 20 ? n : 2;
			s = parseFloat((s + '').replace(/[^\d\.-]/g, '')).toFixed(n) + '';
			var l = s.split('.') [0].split('').reverse(),
				r = s.split('.') [1];
			var  t = '';
			for (var i = 0; i < l.length; i++) {
				t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? ',' : '');
			}
			var res = t.split('').reverse().join('') + '.' + r;
			if(res.indexOf(',') == 0){
				res = res.substring(1);
			}
			if(res.indexOf('-,') == 0){
				res = "-"+res.substring(2);
			}
			return res;
		}
	  },
	  created(){
	  	this.prohvalue = this.perHeight();
	  }
	}
</script>

<style lang="scss" scoped>
.finatop{
	padding: 20px 0;
	background-color: rgba(34,28,54,.8);
	border-radius: 5px;
	>div{
		float: left;
		width: 33.27%;
		height: 100%;
		border-right: 1px solid #443c5e;
		>h4{
			padding-left: 20px;
			font-size: 14px;
			color: rgba(255,255,255,.8);
			line-height: 36px;
		}
		>span{
			padding-left: 20px;
			font-size: 40px;
 			color: #33d7b5;
 			display: block;
 			>em{
 				float: left;
 			}
 			>i{
 				float: left;
 			}
 			>b{
 				float: left;
 			}
		}
	}
	>div:nth-of-type(1){
		padding-left: 20px;
		width: 31.73%;
	}
	>div:nth-of-type(3){
		float: right;
		border-right: 0;
	}
}
</style>


