<template>
  <div class="finsta">
  	<bossfin-top v-bind:toptotalshow="this.toptotalshow"></bossfin-top>
  	<bossfin-con v-bind:getenddats="this.getenddats"
    v-bind:getdatasafter="this.getdatasafter"></bossfin-con>
  </div>
</template>

<script>
//引入头部组件
import BossfinTop from './bossfintop.vue'
//引入内容组件
import BossfinCon from './bossfincon.vue'
export default {
  name: 'Bossfin',
  components: {
    BossfinTop, BossfinCon
  },
  data () {
    return {
      curenddate: '',
      lastenddate: '',
      toptotalshow: '',
    }
  },
  methods: {
    //获取昨天和上月的最后一天的日期
    getenddats(curenddate, lastenddate){
      this.curenddate = curenddate;
      this.lastenddate = lastenddate;
    },
    //代理走的方式
    getlocation(){
      if(location.host === 'qddata.qdingnet.com'){
          return location.origin + '/getHttpData?url='
      }else if(location.host === 'qa-qddata.bigdata.qdingnet.com'){
          return location.origin + '/getHttpData?url='
      }else{
          return 'http://dev-qddata.bigdata.qdingnet.com/getHttpData?url='
      }
    },
    //对地址进行编码
    proxy(str, param){
      var paramStr = '?';   
      for (let i in param) {
        paramStr += (i + '=' + param[i] + '&')
      }  
      if(paramStr == '?'){
        paramStr = '';
      }
      if(location.host === 'qddata.qdingnet.com'){
        return encodeURIComponent("http://yushanfang.bigdata.qdingnet.com"+str + paramStr);
      }else if(location.host === 'qabigdata.qdingnet.com'){
        return encodeURIComponent("http://qa-yushanfang.bigdata.qdingnet.com"+str + paramStr);
      }else{
        return encodeURIComponent("http://dev-yushanfang.bigdata.qdingnet.com"+str + paramStr);
      }
    },
    //获取所有的数据
		getalldatas(){
			this.$http.get(this.getlocation() + this.proxy('/dataApiQuery/qintianjian_c_order_wuyejiaofei', {dt: this.curenddate})).then((res) => {
        if(res.data && res.data.data && res.data.data != '' && res.data.data.length != 0){
          let list = res.data.data;
          let linedatas = [],
              bardatas = [],
              linealldatas = [],
              baralldatas = [];
          for(let i=0; i<list.length; i++){
            if(list[i].data_type == 'zong'){
              this.toptotalshow = list[i];
            }else if(list[i].data_type == 'group_day'){
              linedatas.push(list[i]);
            }else if(list[i].data_type == 'month'){
              bardatas.push(list[i]);
            }
          }
          linealldatas.push(linedatas);
          baralldatas.push(bardatas);

          //获取上期的数据
          this.$http.get(this.getlocation() + this.proxy('/dataApiQuery/qintianjian_c_order_wuyejiaofei', {dt: this.lastenddate})).then((res) => {
            if(res.data && res.data.data && res.data.data != '' && res.data.data.length != 0){
              let list_2 = res.data.data;
              let linedatas_2 = [];
              for(let i=0; i<list_2.length; i++){
                if(list_2[i].data_type == 'group_day'){
                  linedatas_2.push(list_2[i]);
                }
              }
              linealldatas.push(linedatas_2);
              this.childfun(linealldatas, baralldatas)
            }else{
              linealldatas.push([]);
              baralldatas.push([]);
              this.childfun(linealldatas, baralldatas)
            }
          })
        }else{
          this.toptotalshow = '';
          this.childfun([[], []], [[], []])
        }
      })
    },
    //得到数据之后调取子组件的函数
    getdatasafter(fun){
      this.childfun = fun;
    }
  },
  created(){
    
  },
  mounted(){
    this.getalldatas();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



