<template>
	<div class="finacon">
		<h3>趋势对比<span>({{laststartdate}}至{{lastenddate}}对比{{curstartdate}}至{{curenddate}})</span></h3>
		<div class="finacon_charts" :style="{height: (prohvalue*2.8+30)+'px'}">
			<div :style="{height: prohvalue*1.4+'px'}">
				<bossfin-charts v-bind:type="1" v-bind:handleafter="this.handleafter_1"></bossfin-charts>
			</div>
			<div :style="{height: prohvalue*1.4+'px'}">
				<bossfin-charts v-bind:type="2" v-bind:handleafter="this.handleafter_2"></bossfin-charts>
			</div>
			<div :style="{height: prohvalue*1.4+'px'}">
				<bossfin-charts v-bind:type="3" v-bind:handleafter="this.handleafter_3"></bossfin-charts>
			</div>
			<div :style="{height: prohvalue*1.4+'px'}">
				<bossfin-charts v-bind:type="4" v-bind:handleafter="this.handleafter_4"></bossfin-charts>
			</div>
		</div>
	</div>
</template>

<script>
	//引入图表模块
	import BossfinCharts from './bossfincharts.vue'
	export default {
	  name: 'Bossfincon',
	  props: ['getenddats', 'getdatasafter'],
	  components: {
	    BossfinCharts
	  },
	  data () {
	    return {
	      prohvalue: 0,
	      curstartdate: '',
	      curenddate: '',
	      laststartdate: '',
		  lastenddate: '',
		  chartsnums: 0,
	    }
	  },
	  methods: {
	  	//根据屏幕适配高度
		perHeight() {
		    let clientWidth = document.body.clientWidth - 80;
		    let height = (clientWidth / 3) * (9 / 16);
		    return height;
		},
		//获取当前月的日期及上个月的日期
		curandlastdate(){
			let curyear = (new Date()).getFullYear();
			let curmonth = (new Date()).getMonth()+1;
			let curday = (new Date()).getDate() - 1 == 0?(new Date()).getDate():((new Date()).getDate() - 1);
			this.curstartdate = curyear + '-' + (curmonth < 10?'0'+curmonth:curmonth) + '-01';
			this.curenddate = curyear + '-' + (curmonth < 10?'0'+curmonth:curmonth) + '-' + (curday < 10?'0'+curday:curday);
			if((curmonth-1) == 0){
				this.laststartdate = (curyear-1) + '-12-01';
				this.lastenddate = (curyear-1) + '-12-31';
			}else{
				let arr1 = [1, 3, 5, 7, 8, 10, 12];
				let arr2 = [4, 6, 9, 11];
				if((curmonth-1) == 2){
		            if(year%4==0 && year%100!=0 || year%400==0){
		            	this.laststartdate = curyear + "-02-01";
						this.lastenddate = curyear + "-02-29";
		            }else{
		                this.laststartdate = curyear + "-02-01";
						this.lastenddate = curyear + "-02-28";
		            }
		        }else{
		        	let comMonth2 = (curmonth-1) < 10 ? '0' + (curmonth-1) : (curmonth-1);
		            if(arr1.indexOf(curmonth-1) >= 0){
		            	this.laststartdate = curyear + "-" + comMonth2 + "-01";
						this.lastenddate = curyear + "-" + comMonth2 + "-31";
		            }else if(arr2.indexOf(curmonth-1) >= 0){
		                this.laststartdate = curyear + "-" + comMonth2 + "-01";
						this.lastenddate = curyear + "-" + comMonth2 + "-30";
		            }
		        }
			}
			this.getenddats(this.curenddate, this.lastenddate)
		},
		//处理数据
		handledatas(linealldatas, baralldatas){
			let line1s = [this.conversion(linealldatas[0], 'order_num'), this.conversion(linealldatas[1], 'order_num')],
				line1x = [this.conversion(linealldatas[0], 'date').length > this.conversion(linealldatas[1], 'date').length?this.conversion(linealldatas[0], 'date'):this.conversion(linealldatas[1], 'date')],
				line2s = [this.conversion(linealldatas[0], 'gmv'), this.conversion(linealldatas[1], 'gmv')],
				line2x = [this.conversion(linealldatas[0], 'date').length > this.conversion(linealldatas[1], 'date').length?this.conversion(linealldatas[0], 'date'):this.conversion(linealldatas[1], 'date')],
				bar1s = [this.conversion(baralldatas[0], 'order_num')],
				bar1x = [this.conversion(baralldatas[0], 'date')],
				bar2s = [this.conversion(baralldatas[0], 'gmv')],
				bar2x = [this.conversion(baralldatas[0], 'date')],
				alldatas = [];
			alldatas.push([line1s, line1x])
			alldatas.push([line2s, line2x])
			alldatas.push([bar1s, bar1x])
			alldatas.push([bar2s, bar2x])
			this.childfun1(alldatas[0]);
			this.childfun2(alldatas[1]);
			this.childfun3(alldatas[2]);
			this.childfun4(alldatas[3]);
		},
		//将对象转为数组
		conversion(res, ming){
			let arr = [];
			for(let i=0; i<res.length; i++){
				arr.push(res[i][ming]?res[i][ming]:0)
			}
			return arr;
		},
		//处理完数据之后调取charts图表展示
		handleafter_1(fun){
			this.childfun1 = fun;
		},
		handleafter_2(fun){
			this.childfun2 = fun;
		},
		handleafter_3(fun){
			this.childfun3 = fun;
		},
		handleafter_4(fun){
			this.childfun4 = fun;
		}
	  },
	  created(){
		this.chartsnums = 4;
	  	this.curandlastdate();
		this.prohvalue = this.perHeight();
		this.getdatasafter(this.handledatas)
	  },
	}
</script>

<style lang="scss" scoped>
.finacon{
	width: 100%;
	padding-top: 36px;
	>h3{
		font-size: 18px;
		color: #fff;
		line-height: 18px;
		margin-bottom: 20px;
		padding-left: 10px;
		>span{
			font-size: 12px;
			color: rgba(255, 255, 255, .8);
			line-height: 12px;
		}
	}
	.finacon_charts{
		width: 100%;
		>div{
			width: 48.81%;
			float: left;
			margin-right: 30px;
			border-radius: 5px;
		}
		>div:nth-of-type(2n){
			margin-right: 0;
			float: right;
		}
		>div:nth-of-type(1){
			margin-bottom: 30px;
		}
		>div:nth-of-type(2){
			margin-bottom: 30px;
		}
	}
}
</style>


