<template>
	<div class="finacharts">
		<div ref="chartsshow" v-bind:style="{height: prohvalue*1.4+'px'}">
			
		</div>
	</div>
</template>

<script>
	// 引入基本模板
	let echarts = require('echarts/lib/echarts')
	// 引入柱状图组件
	require('echarts/lib/chart/bar')
	// 引入折线图组件
	require('echarts/lib/chart/line')
	// 引入提示框和title组件
	require('echarts/lib/component/tooltip')
	require('echarts/lib/component/title')
	//引入option
	import Optionlineandbar from '../../static/javascript/optionlineandbar.js'
	export default {
	  name: 'Bossfintop',
	  props: ['type', 'handleafter'],
	  data () {
	    return {
	      prohvalue: 0,
	    }
	  },
	  methods: {
	  		//根据屏幕适配高度
			perHeight() {
					let clientWidth = document.body.clientWidth - 80;
					let height = (clientWidth / 3) * (9 / 16);
					return height;
			},
			//创建charts图表
			setchartsshow(alldatas){
				let titles = ['日缴费订单数', '日缴费金额', '月度总缴费订单数', '月度总缴费金额'];
				let legs = [['上月缴费单数', '本月缴费单数'], ['上月缴费总金额', '本月缴费总金额'], '月度总缴费订单数', '月度总缴费金额'];

				let charTarget = echarts.init(this.$refs.chartsshow);
				let optiondata = this.type > 2 ? JSON.stringify(Optionlineandbar.optionbarcharts):JSON.stringify(Optionlineandbar.optionlinecharts)
				let optionReach= JSON.parse(optiondata);
				charTarget.showLoading({
					text: ' ',
					effect: 'whirling',
					color: '#fff',
					maskColor: 'rgba(13,13,13, 0.6)',
				});
				charTarget.clear();
				
				if(this.type == 1){
					optionReach.toolbox.feature.restore.show = false;
					optionReach.toolbox.feature.dataZoom.show = false;
					optionReach.toolbox.feature.dataView.show = false;
				}else{
					optionReach.toolbox.feature.dataZoom.show = false;
				}
				if(alldatas){
					if(this.type <= 2){
						optionReach.xAxis[0].data = alldatas[1][0];
						optionReach.series[0].data = alldatas[0][1];
						optionReach.series[0].name = legs[this.type-1][0];
						optionReach.series[1].data = alldatas[0][0];
						optionReach.series[1].name = legs[this.type-1][1];
						optionReach.legend.data = legs[this.type-1]
						if(this.type == 2){
							 optionReach.tooltip.formatter = this.toHtml.bind(this);
						}
					}else{
						optionReach.xAxis[0].data = alldatas[1][0];
						optionReach.series[0].data = alldatas[0][0];
						optionReach.series[0].name = legs[this.type-1];
						if(this.type == 4){
							 optionReach.tooltip.formatter = this.toHtml.bind(this);
						}
					}
					optionReach.title.text = titles[this.type-1];
					optionReach.yAxis[0].axisLabel.formatter = function(value){
						return (value/1000).toLocaleString() + 'k';
					}
				}
				
				charTarget.hideLoading();
				charTarget.setOption(optionReach);
			},
			//数值千分位的同时保留两位小数，四舍五入
			fmoney(s, n){
				n = n > 0 && n <= 20 ? n : 2;
				s = parseFloat((s + '').replace(/[^\d\.-]/g, '')).toFixed(n) + '';
				var l = s.split('.') [0].split('').reverse(),
					r = s.split('.') [1];
				var  t = '';
				for (var i = 0; i < l.length; i++) {
					t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? ',' : '');
				}
				var res = t.split('').reverse().join('') + '.' + r;
				if(res.indexOf(',') == 0){
					res = res.substring(1);
				}
				if(res.indexOf('-,') == 0){
					res = "-"+res.substring(2);
				}
				return res;
			},
			toHtml(params){
				let content = '';
				for(var i=0; i<params.length; i++){
					if(params[i].value){
						content += '<p>' + (params[i].seriesName + ': ' + this.fmoney(params[i].value, 2)) +  '</p>';
					}
				}
				
				return '<div><p>' + params[0].axisValue + '</p>' + content + '</div>'  
			}
	  },
	  created(){
			this.prohvalue = this.perHeight();
	  },
	  mounted(){
			//创建charts图表
			this.setchartsshow()
			this.handleafter(this.setchartsshow)
	  }
	}
</script>

<style lang="scss" scoped>
.finacharts{
	width: 100%;
	>div{
		width: 100%;
	}
}
</style>


